.last {
    overflow: hidden;

    &__page_descr {
        max-width: 600px;
        color: #000000;
        font-family: $font-family-sans-serif;
        font-size: 36px;
        line-height: 46px;
        margin-top: 0;
        margin-left: 140px;
    }

    &__wait {
        color: #000000;
        font-family: $font-family-sans-serif;
        font-size: 36px;
        font-weight: 700;
        margin-top: 65px;
        margin-left: 140px;
    }

    &__advice {
        color: #000000;
        font-family: $font-family-sans-serif;
        font-size: 20px;
    }

    &__footer {
        position: relative;
        display: flex;
        margin-left: -30px;
        margin-top: 240px;
    }

    &__mail {
        width: 293px;
        height: 22px;
        color: #000000;
        font-family: $font-family-sans-serif;
        font-size: 20px;
        font-weight: 700;
        margin-left: 20px;
    }

    &__rocket {
        position: fixed;
        bottom: 0;
        left: 0;
        max-width: 400px;
        height: auto;
    }

    &__logo {
        margin: 25px 0 40px 35px;
    }

    @media screen and (min-width: 1200px) {
        &__footer {
            margin-top: 140px;
        }
    }
}